@import "~milligram";

@mixin readyAnim {
  0%   { opacity: 0; left: 0; }
  5%   { opacity: 100;  }
  15%  { opacity: 0; left: 100%; }
  100% { opacity: 0; left: 100%; }
}
@-webkit-keyframes readyAnim {
  @include readyAnim;
}
@-moz-keyframes readyAnim {
  @include readyAnim;
}
@-o-keyframes readyAnim {
  @include readyAnim;
}
@keyframes readyAnim {
  @include readyAnim;
}

body {
  padding: 0;
  margin: 0;
  background-color: rgb(243, 243, 243);
  font-family: Lato, Helvetica Neue, Helvetica, sans-serif;
}

.switch {
  input[type=checkbox]{
    height: 0;
    width: 0;
    visibility: hidden;
  }

  label {
    cursor: pointer;
    text-indent: -9999px;
    width: 130px;
    height: 60px;
    background: grey;
    display: block;
    border-radius: 100px;
    position: relative;
  }

  label:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    width: 50px;
    height: 50px;
    background: #fff;
    border-radius: 90px;
    transition: 0.3s;
  }

  input:checked + label {
    background: #bada55;
  }

  input:checked + label:after {
    left: calc(100% - 5px);
    transform: translateX(-100%);
  }

  label:active:after {
    width: 90px;
  }
}
